<template>
  <div class="inward">
    <v-tabs
      v-model="tab"
      center-active
      show-arrows
      class="mb-16 medium"
      color="#D40A0A"
    >
      <v-tabs-slider color="#D40A0A" />
      <v-tab>Facilities & Capabilities</v-tab>
      <v-tab>Products & Services</v-tab>
      <v-tab>Rental</v-tab>
      <v-tab>MPD / RCD</v-tab>
      <v-tab>Our Clients</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-expansion-panels class="pa-1">
          <v-expansion-panel
            v-for="(item, i) in facilities"
            :key="i"
          >
            <v-expansion-panel-header class="t-h4 medium">
              {{ item.header }}
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-list>
                <v-list-item
                  v-for="(subitem, j) in item.subitems"
                  :key="j"
                >
                  <v-list-item-content>
                    <v-list-item-title class="t-h5 medium">{{ subitem }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-tab-item>
      <v-tab-item>
        <v-expansion-panels class="pa-1">
          <v-expansion-panel
            v-for="(item, i) in products"
            :key="i"
          >
            <v-expansion-panel-header class="t-h4 medium">
              {{ item.header }}
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-list>
                <v-list-item
                  v-for="(subitem, j) in item.subitems"
                  :key="j"
                >
                  <v-list-item-content>
                    <v-list-item-title class="t-h5 medium">{{ subitem.title }}</v-list-item-title>
                    <v-list-item-subtitle
                      v-for="(subtitle, k) in subitem.subtitles"
                      :key="k"
                      class="text-wrap pt-6 medium"
                    >
                      {{ subtitle }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
              <v-img :src="require(`@/assets/products-${i + 1}.jpg`)" />
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-tab-item>
      <v-tab-item>
        <v-row align="center">
          <v-col cols="12" md="6" class="pr-md-6">
            <v-img src="@/assets/rental-1.jpg" />
          </v-col>
          <v-col cols="12" md="6" class="pl-md-6">
            <div class="t-h4 medium">BOP Stack 1</div>
            <div class="t-h5 medium py-8">
              13-5/8" 10K Double Ram BOP, Cameron Type U
              <br />
              13-5/8" 10K Single Ram BOP, Cameron Type U
              <br />
              13-5/8" 5K GK Annular BOP, Hydril
            </div>
            <div class="t-h4 medium">BOP Stack 2</div>
            <div class="t-h5 medium py-8">
              13-5/8" 5K Double Ram BOP, Hydril
              <br />
              13-5/8" 5K GX Annular BOP, Hydril
            </div>
            <div class="t-h4 medium">Please email <a href="mailto:sales@rmes.com.sg">sales@rmes.com.sg</a> for further enquiries.</div>
          </v-col>
        </v-row>
      </v-tab-item>
      <v-tab-item>
        <div class="t-h4 medium">The RMES MPD/RCD is designed to be a flexible system that can be constructed to suit the customers' requirements.</div>
        <div class="t-h4 medium">THE ADAPTIVE SYSTEM Available basic configurations are:</div>
        <ul class="my-6 t-h5 medium">
          <li>Below tension ring (BTR)</li>
          <li>Above tension ring (ATR)</li>
          <li>Jack-up (JU)</li>
          <li>Land Drilling (LD)</li>
        </ul>
        <div class="t-h4 medium">The RMES MPD system is designed to be flexible and can be constructed to the requirements of the Oil Company, the Drilling Contractor and the rig or vessels limitations.</div>
        <v-row>
          <v-col cols="6" class="pr-6">
            <v-img src="@/assets/mpd-1.png" height="500px" contain />
          </v-col>
          <v-col cols="6" class="pl-6">
            <v-img src="@/assets/mpd-2.png" height="500px" contain />
          </v-col>
        </v-row>
        <div class="t-h4 bold">RMES MPD Joint</div>
        <div class="t-h4 bold mt-8">RMES Rotating Control Device</div>
        <ul class="my-6 t-h5 medium">
          <li>Dual Packer Elements</li>
          <li>ID Through Bearing Assembly 9-1/4</li>
          <li>ID Through Bore Protector 17-5/8</li>
          <li>ID Through RCD Housing 18-3/4</li>
        </ul>
        <div class="t-h4 bold mt-8">RMES MPD Annular</div>
        <ul class="my-6 t-h5 medium">
          <li>Quick Close or Standard</li>
        </ul>
        <div class="t-h4 bold mt-8">RMES Flow Spool</div>
        <ul class="my-6 t-h5 medium">
          <li>2 x 6 Flowlines</li>
          <li>Dual Block Isolation Ball Valves Standard</li>
        </ul>
        <div class="t-h4 bold mt-8">Advanced Element Simulation</div>
        <v-img class="mt-8" src="@/assets/mpd-3.jpg" width="100%" contain />
        <v-row class="mt-8">
          <v-col
            cols="4"
            v-for="i in 3"
            :key="i"
          >
            <v-img :src="require(`@/assets/mpd-${i + 3}.jpg`)" />
          </v-col>
        </v-row>
      </v-tab-item>
      <v-tab-item>
        <v-list>
          <v-list-item
            v-for="customer in customers"
            :key="customer"
          >
            <div class="t-h4 medium">{{ customer }}</div>
          </v-list-item>
        </v-list>
      </v-tab-item>
    </v-tabs-items>
    <div class="mb-16"></div>
  </div>
</template>

<script>
export default {
  data: () => ({
    tab: null,
    facilities: [{
      header: "Cranes",
      subitems: ["50T Overhead Gantry Cranes", "20T Overhead Cranes", "16T Overhead Gantry Crane", "10T Overhead Cranes", "6T Overhead Cranes", "5T Overhead Cranes", "100T Capacity Spreader Bar"],
    }, {
      header: "Welding",
      subitems: ["Cladding (Inconel 625, SS 316L, Aluminum Bronze)", "Automatic welding SAW", "Semi-automatic welding FCAW & MIG", "Manual welding TIG & SMAW"],
    }, {
      header: "Machining",
      subitems: ["CNC 5-face double column gantry machining centers", "Large CNC horizontal boring-milling", "Large CNC vertical turn-mill", "Large CNC horizontal turn-mill", "CNC horizontal lathes", "CNC vertical machining centers", "And various supporting machines"],
    }, {
      header: "Inspection",
      subitems: ["D&I", "Positive material identification", "Hardness testing", "Ultrasonic testing", "Magnetic particle testing", "Dye penetrant testing"],
    }, {
      header: "Facility & Equipment",
      subitems: ["Post Weld Heat Treatment Furnace", "High Pressure Waterjet Cleaning", "Blasting and Painting", "Hydraulic Power Units", "Diesel Powered Generator", "Electrical Test Units", "Pneumatic Test Units", "High Pressure Test Pits", "BOP Stacking Pads", "80 feet boom lift", "60 feet boom lift", "Scissor lift"],
    }],
    products: [{
      header: "API Manufacturing",
      subitems: [{
        title: "API 6A - PSL 1, PSL 2, PSL 3",
        subtitles: ["Adapter and Spacer Spools, Cross-Over Connectors, Flanged Connectors, Other End Connectors, Tees and Crosses", "Threaded Connectors, Top Connectors", "QMS Exclusions - None"],
      }, {
        title: "API 16A",
        subtitles: ["Drilling Spools, Adapters", "QMS Exclusions - None"],
      }, {
        title: "API 16C",
        subtitles: ["Choke & Kill Line, Buffer Chamber"],
      }, {
        title: "API 17D",
        subtitles: ["Other End Connectors"],
      }],
    }, {
      header: "Fabrication, Assembly & Testing",
    }, {
      header: "Inspection, Repair, Modification & Re-Manufacturing",
    }, {
      header: "BOP Stacking & Services",
    }, {
      header: "High Pressure testing",
    }, {
      header: "On-site Inspection, Machining, Fit-up and Welding",
    }],
    customers: ["Black & Veatch",
      "Cameron, A Schlumberger Company",
      "Diamond Offshore",
      "Ensco",
      "Forum Energy Technologies",
      "Heerema Marine",
      "Japan Drilling Company",
      "Josco",
      "KCA Deutag",
      "Keppel FELS",
      "MacGregor",
      "Maersk Drilling",
      "MHWirth",
      "National Oilwell Varco",
      "Noble Drilling",
      "Oceaneering Service",
      "Opus Offshore",
      "Parker Drilling",
      "Risers International",
      "Seadrill",
      "SEVAN Drilling",
      "SH Group",
      "Songa Offshore",
      "Songa Opus Offshore",
      "Transocean",
      "UMW JDC Drilling"
    ],
  }),
  mounted() {
    this.tab = this.$route.params.tab;
  },
};
</script>
