import Vue from "vue"
import VueRouter from "vue-router"

import Home from "@/views/Home.vue"
import AboutUs from "@/views/AboutUs.vue";
import OurBusiness from "@/views/OurBusiness.vue";
import QHSE from "@/views/QHSE.vue";
import Media from "@/views/Media.vue";
import Resources from "@/views/Resources.vue";
import CareerOpportunities from "@/views/CareerOpportunities.vue";
import ContactUs from "@/views/ContactUs.vue";
import Privacy from "@/views/Privacy.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about-us",
    name: "AboutUs",
    component: AboutUs,
  },
  {
    path: "/our-business",
    name: "OurBusiness",
    component: OurBusiness,
    props: true,
  },
  {
    path: "/qhse",
    name: "QHSE",
    component: QHSE,
    props: true,
  },
  {
    path: "/media",
    name: "Media",
    component: Media,
  },
  {
    path: "/resources",
    name: "Resources",
    component: Resources,
  },
  {
    path: "/career-opportunities",
    name: "CareerOpportunities",
    component: CareerOpportunities,
  },
  {
    path: "/contact-us",
    name: "ContactUs",
    component: ContactUs,
  },
  {
    path: "/privacy",
    name: "Privacy",
    component: Privacy,
  },
];

const router = new VueRouter({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
